import React from 'react';
import Bottom from './Bottom';
import './store.css';
const ruler=(props)=>{
    return(
        <div>
            <div>
                <i style={{fontSize:50,display:"block"}} 
                className='iconfont icon-fanhui'
                onClick={() =>props.history.go(-1)}></i>
                <img src={require('./imgs/b.jpg')} style={{height:300,width:300,margin:40}}/>
            </div>
            <div>
                <Bottom/>
                <button className='button1'>加入购物车</button>
                <button className='button2'>立即购买</button>
            </div>
        </div>
    )
}
export default ruler;